<script>
import { reactive } from 'vue';
import axios from 'axios';
import { useStore } from 'vuex';
import helper from '../store/helper.js'

export default {
  name: 'Home',
  setup() {

    var store = useStore();
    var state = reactive({
      homefeed: [],
      end_cursor: '',
      hasNextPage: false,
      is_adding: false,
      is_liking: false
    });

    function loadHome() {
      state.isloading = true;
      axios.post('/gethomefeed', { username: store.state.user,  end_cursor: state.end_cursor })
      .then(res => {
        res = res.data;
        console.log(res);
        res.timeline.edges.filter(el => state.homefeed.push(el.node));
        state.homefeed = state.homefeed.map(el => {
          el.hasPost = helper.hasThisPost(el, store);
          return el;
        });
        state.end_cursor  = res.timeline.page_info.end_cursor;
        state.hasNextPage = res.timeline.page_info.has_next_page;
        state.isloading   = false;
      })
    }

    function loadMore() {
      loadHome();
    }

    function addthisPost(post) {
      if( post.hasPost ) return;
      post.is_adding = true;
      console.log(post);
      axios.post('/addpost', { post, username: store.state.user })
      .then(res => {
        res = res.data;
        console.log(res);
        post.is_adding = false;
        if( res.addpost ) {
            store.state.userdata.posts.push(res.addpost);
        }
      })
    }

    function likePost(post) {
      var mediaId = post.id;
      post.is_liking = true;
      axios.post('/postaction', {username: store.state.user, mediaId })
      .then(res => {
        res = res.data;
        console.log(res);
        if( res.mediaId.status == 'ok') {
          post.viewer_has_liked = true;
          post.is_liking = false;
        }
      })
    }
    /// Load obn MOute the ap
    // onMounted(() => loadHome())
    
    return {
      state,
      loadMore,
      addthisPost,
      likePost
    }

  }
}
</script>

