<template>
  <div class="container main-body">
    <Header :users="store.state.users"/>
    <div id="nav" class="text-center">
      <router-link to="/">Home</router-link> |
      <span v-if="!store.state.user">
        <router-link to="/login">Login</router-link> |
      </span>
      <router-link to="/profile">profile</router-link> |
      <router-link to="/posts">Posts</router-link> |
      <router-link to="/accounts">Accounts</router-link> |
    </div>
    <router-view/>

    <p class="border boder-mute">{{ store.state.user }}</p>
    <p class="border boder-mute">{{ store.state.users }} : {{ Object.keys(store.state.userdata).length }}</p>
    <p class="border boder-mute"><pre>{{ store.state.userdata.accounts }}</pre></p>
    
    <Footer/> 
  </div>
</template>

<script>
import { useStore } from 'vuex';
import { computed, onBeforeMount } from 'vue';
import Header from './components/Header.vue';
import Footer from './components/Footer.vue';
import cookie from './store/helper';


export default {
  components: {
    Header,
    Footer,
  },
  setup() {

    const store = useStore();
    const users = computed(() => store.state.users);

    function SelectIt(username) {
      store.dispatch('SELECTUSER', username);  
    }

    onBeforeMount(async () => {
      if( cookie.hasCookie('user') && cookie.getCookie('user') ) {
        await store.dispatch('SELECTUSER', cookie.getCookie('user'));
        console.log('cookig', cookie.getCookie('user') );
      }
    })

    return {
      store,
      users,
      SelectIt
    }

  },
}
</script>

