import axios from 'axios';
import _ from 'lodash';
import lid from 'lodash-id';
import { createStore } from 'vuex'
// import { users } from '../assets/users.js'
_.mixin(lid);

export default createStore({

  state: {
    user: '',
    users: [],
    userdata: {}
  },

  mutations: {
    async SELECT_USER(state, username) {
      state.user = username;
    },
    async setUserData(state, username) {
      // console.log('axois');
      // console.log('state.userdata', state.userdata, _.size(state.userdata));
      if( _.size(state.userdata) <= 0 && state.userdata?.username != username ) {
          // console.log('state.userdata not found!');
          axios.post('/getuser', {username}).then(res => {
            state.userdata = res.data;
            // console.log(res);
          }).catch(err => {
            console.log(err);
          })
      }
    },
    MSETNEWUSER(state, response) {
      return new Promise(function(resolve) {
        state.user = response.username;
        response.username = response.user.username;
        state.users.push(response);
        state.userdata = response;
        resolve(true);
      });
    },
    pushNewUser(state, username) {
      state.users.push(username);
    },
    MUpdateNewAccount(state, newaccount) {

      var isavailable = false;
      for (let i = 0; i < state.userdata.accounts.length; i++) {
        const el = state.userdata.accounts[i];
        console.log(+el.id, +el.id == +newaccount.id, +newaccount.id);
        if( +el.id == +newaccount.id ) {
          state.userdata.accounts[i] = newaccount;
          isavailable = true;
          break;
        }
      }
      console.log('isavailable',isavailable);
      if( ! isavailable ) state.userdata.accounts.push(newaccount);
      
    },
    MUpdateEditAccount(state, account) {
      for (let i = 0; i < state.userdata.accounts.length; i++) {
        const el = state.userdata.accounts[i];
        if( +el.id == +account.id ) {
          state.userdata.accounts[i] = account;
          break;
        }
      }
    },
    async Mdopost(state) {
      axios.post('/dopost', {username: state.user, posts: state.userdata.posts}).then(res => {
        console.log(res.data);
      }).catch(err => {
        console.log(err);
      })
    },
    async MremoveAccount(state, id) {
      var removedd = _.removeById(state.userdata.accounts, id);
      console.log(removedd);
      console.log(state.userdata.accounts);
      axios.post('/doaccout', { username: state.user, action: 'update', accounts: state.userdata.accounts})
      .then(res => {
        console.log(res.data);
      })
    },


    
  },

  actions: {
    async SELECTUSER({ commit }, username) {
      return new Promise(function (resolve) {
        commit('SELECT_USER', username);
        commit('setUserData', username);
        commit('pushNewUser', username)
        resolve(true);
      })
    },
    async SETNEWUSER({ commit }, response) {
      commit('MSETNEWUSER', response);
    },
    async UpdateNewAccount({commit}, newaccount) {
      return new Promise(function (resolve) {
        commit('MUpdateNewAccount', newaccount);
        resolve(true);
      })
    },
    async UpdateEditAccount({commit}, account) {
      return new Promise(function (resolve) {
        commit('MUpdateEditAccount', account);
        resolve(true);
      })
    },
    async dopost({commit}) {
      return new Promise(function (resolve) {
        commit('Mdopost');
        resolve(true);
      })
    },
    async removeAccount({commit}, id) {
      return new Promise(function (resolve) {
        commit('MremoveAccount', id);
        resolve(true);
      })
    },
    
    
  },

  getters: {
    getUserPosts(state){
      return state.userdata.posts;
    }
  },

  modules: {

  }
})
