import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue';
import cookie from '../store/helper';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue'),
    meta: {
      requiresNoAuth: true
    }
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => import( /* webpackChunkName: "about" */ '../views/Profile.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/posts',
    name: 'Posts',
    component: () => import( /* webpackChunkName: "about" */ '../views/Posts.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/accounts',
    name: 'Accounts',
    component: () => import( /* webpackChunkName: "about" */ '../views/Accounts.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/searchprofile',
    name: 'SearchProfile',
    component: () => import( /* webpackChunkName: "about" */ '../views/SearchProfile.vue'),
    meta: {
      requiresAuth: true
    }
  },
]



const router = createRouter({
  history: createWebHistory(),
  routes
});

router.beforeEach((to, from, next) => {
//   console.log('from', from);
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if ( cookie.hasCookie('user') ) {
      console.log('hascookie');
      next();
    } else {
      next({
          path: '/login'
      })
    }
  } else if(to.matched.some(record => record.meta.requiresNoAuth)) {
    console.log("cookie.hasCookie('user')", cookie.hasCookie('user'));
    if ( cookie.hasCookie('user') ) {
      next({
        path: '/'
      })
    } else {
      next();
    }
  } else {
    next()
  }
})

export default router
