<template lang="pug">
.nl-header.border.border-mute.d-flex.justify-content-between.mt-4.mb-4.pl-3.pr-3.pt-2.pb-2
  .left-wrap  
    | Menu
  .right-wrap
      a.seleted-account(href="#")
        img(:src="userdata.profile_pic_url")
        span.accounts {{ user }}
      .account-select.border.border-mute
        ul.accounts.m-0.p-0
          a(href="#", v-for="user in users" @click.prevent="SelectIt(user.username)")
            li.account(id="123") 
            img(src="https://mdbcdn.b-cdn.net/img/new/standard/city/047.jpg") 
            | {{ user.firstName + ' ' + user.lastName }}
</template>

<script>
// import { computed } from 'vue';
import { useStore } from 'vuex'
import { computed } from 'vue'

export default {
    name: 'Header',
    setup() {
      var store = useStore();
      var user = computed(() => store.state.user);
      // var fullname = computed(() => store.state.userdata);
      var users = computed(() => store.state.users);
      var userdata  = computed(() => store.state.userdata);
      function SelectIt(username) {
        store.dispatch('SELECTUSER', username);  
      }

      return {
        user,
        users,
        userdata,
        SelectIt
      }

    },
}
</script>

<style lang="less">
.nl-header {
  font-size: 16px;
  .right-wrap {
    position: relative;
    img {
      width: 18px;
      height: 18px;
      border-radius: 50%;
      border: 1.5px solid #8e8e8e;
      object-fit: cover;
      vertical-align: middle;
      margin-right: 5px;
      box-shadow: 0px 0px 1px #777373bf;
    }
    .account-select {
      position: absolute;
      display: none;
      right: 0;
      top: 0;
      width: max-content;
      margin-top: 32px;
      background: white;
      box-shadow:  -5px 5px 5px rgba(0, 0, 0, 0.2);
      &:before {
        content: ' ';
        border-top: 5px solid #ccc;
        position: absolute;
        top: -5;
        right: -1rem; 
      }
      &.show {
        display: block;
      }
      .accounts {
        list-style: none;
        & > a {
          padding: 10px 20px;
          display: block;
          border-bottom: 1px solid #ccc;
          &:last-child {
            border-bottom: none;
          }
        }
      }
    }
  }
}
</style>